body, h1 {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
    Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}

body {
  font-family: sans-serif;
  background-color: #212121;
  color: #fff;
}

.headerWithBar {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border-style: none none solid none;
  border-width: 1px;
  border-color: #888;
}

.logo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.logo img {
  margin-right: 0.5rem;
}

.toggleBack {
  padding: 0.5rem;
  margin: 0.5rem 1rem;
  width: auto;
  cursor: pointer;
  border-radius: 20px;
  border: 1px solid #888888;
  text-align: center;
}


.tagsList {
  list-style-type: none;
  padding: 0;
}

.ais-SearchBox {
  width: 35%;
  color: white;
}

.ais-SearchBox-form {
  color: white;
}

.ais-SearchBox-input {
  background-color: #121212;
  border: 1px solid #888888;
  color: white;
}

.ais-Pagination {
  margin-top: 1em
}

.ais-Pagination-link {
  background-color: transparent;
  color: white;
}

.ais-Pagination-item--selected .ais-Pagination-link {
  background-color: white;
  color: black;
  border-color: white;
}

.ais-ClearRefinements-button {
  background-color:#fff;
  color: #000;
}

.left-panel {
  float: left;
  width: 10%;
}

.right-panel {
  background-color: #181818;
  margin-left: 10%;
}

.ais-InstantSearch {
  max-width: 95%;
  height: 100vh;
  overflow: hidden;
  margin: 0 auto;
}

.ais-Hits {
  margin: 0;
  padding: 0;

}

.ais-Hits-list {
  margin: 0;
  padding: 0;
}

.ais-Hits-item {
  padding: 1rem;
  margin: 0;
  width: 25%;
  border: none;
  box-shadow: none;
}

.ais-Hits-item img {
  width: 100%;
  height: auto;
  margin-bottom: 1rem;
  cursor: pointer;
}

.hit-name {
  font-weight: bold;
  color: #fff;
  margin: .5em
}

.hit-speakers {
  color: #888;
  font-size: 14px;
  margin: .5em
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  z-index: 100;
}

.modal-main {
  position:fixed;
  background: #222222;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.modal-content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 2rem 4rem;
}

.modal-info {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.modal-main img {
  width: 40%;
  height: auto;
  margin: 0;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.modal-tag-list {
  padding: 0;
  list-style: none;
  color: white;
  margin-top: 3rem;
}

.closeBtn {
  padding: 0.5rem;
  background-color: transparent;
  color: white;
  border: 1px solid #444444;
  cursor: pointer;
}

.closeBtn:hover {
  background-color: white;
  color: black

}

.modal-tag-list li {
  display: block;
  float: left;
  margin: 1rem;
  border-radius: 20px;
  border: 2px solid #888888;
  text-align: center;
  background-color: #444444;
  padding: 0.5rem;
}

.modal-info-left {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.modal-name {
  color: white;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.modal-stats {
  color: #888888;
}

.modal-description {
  color: white;
}

